
import React from 'react'
import { Row, Col, Input, Form } from 'antd';
import "antd/dist/antd.css";

class Student extends React.Component {
    render() {
        return (
            <>
                <Form>
                    <input>
                        <Form.Item label="Note">
                            lable="First Name"
                            type="text"
                            placeholder="Please enter your Name"
    
                        </Form.Item>


                    </input>
                </Form>

            </>
        )
    }
}
export default Student